.sidebar-menu {
    position: absolute;
    -webkit-transform: translateX(0);
    transform: translateX(0);
    -webkit-transition: .3s ease all;
    transition: .3s ease all;
}
.switch-view-text {
    font-size: 0.8rem;
    color: #756F82;
    display: none;
}
.switch-view-text.open {
    display: block;
}
.sidebar-menu .sidebar-text{
    content-visibility: hidden;
}
.sidebar-menu.open .sidebar-text{
    content-visibility: visible;
}
.sidebar-menu.open {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    -webkit-transition: .3s ease all;
    transition: .3s ease all;
    min-width: 200px;
}

.focus {
    filter: invert(15%) sepia(88%) saturate(6012%) hue-rotate(261deg) brightness(101%) contrast(105%);
}